import React, { useEffect, useContext, useRef } from "react";
import { Link } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import Cross from "../assets/covercross.webp";

function Home() {
    const { getRandomVerse, randomVerse } = useContext(VersesContext);
    const hasTestamentData = useRef(false);

    useEffect(() => {
        if (!hasTestamentData.current) {
                getRandomVerse();
            } 
            hasTestamentData.current=true; 
    }, [getRandomVerse])


    return (
        <div>
            <div className="coverpage">
                <div className="booktitle">
                    <Link to="/toc">
                        <p style={{
                                fontFamily: "FrederickatheGreat-Regular",
                                fontSize: "4rem"
                            }}>
                            BIBLIJA
                        </p>
                        <p>
                            SVETO PISMO
                        </p>
                        <small>
                            STAROGA I NOVOGA ZAVJETA
                        </small>
                    </Link>
                </div>

                <Link to="/toc">
                    <img width="240" height="389" id="cross-logo" src={Cross} alt="Cross" />
                </Link>

                <div className="randomverse">
                    <Link 
                        style={{textDecoration: "none", color: "inherit"}} 
                        to={`/verse/${randomVerse.book}/${randomVerse.chapter}/${randomVerse.verse_no}`}
                    >
                        <p>{randomVerse.verse_text}</p>
                    </Link>
                    <small>
                        ~ {randomVerse.name} <em>{randomVerse.chapter}:{randomVerse.verse_no}</em>
                    </small>
                </div>
            </div>
        </div>
    )
}

export default Home;