import React, {lazy, Suspense} from "react";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import Home from "./components/Home";

const OverlayMenu = lazy(() => import("./components/OverlayMenu"));
const About = lazy(() => import("./components/About"))
const TableOfContent = lazy(() => import("./components/TableOfContent"))
const Settings = lazy(() => import("./components/Settings"))
const Book = lazy(() => import("./components/Book"))
const SearchTerm = lazy(() => import("./components/SearchTerm"))
const VerseInChapter = lazy(() => import("./components/VerseInChapter"))

const AppRoutes = () => {
    return(
        <div>
            <BrowserRouter>
                <Suspense fallback={<div>Učitavam...</div>}>
                    <OverlayMenu />
                    <Routes>
                        <Route path="/" element={<Home />}/>
                        <Route path="/toc" element={<TableOfContent />}/>
                        <Route path="/settings" element={<Settings />}/>
                        <Route path="/about" element={<About />}/>
                        <Route path="/book/:book" element={<Book />}/>
                        <Route path="/search" element={<SearchTerm />}/>
                        <Route path="/verse/:book/:chapter/:verse_no" element={<VerseInChapter />}/>
                        <Route path="*" element={<Home />}/>
                    </Routes>
                </Suspense>
            </BrowserRouter>
        </div>
    )
}

export default AppRoutes;